.Content {
  max-width: 60em;
  margin: auto;
  margin-bottom: 10em;
}

.Content-container {
  margin-top: 2em;
  padding: 1em 2em;
  background: none;
  border: 2px #ddd solid;
}

.Content-title {
  text-align: left;
  margin-bottom: 0.2em;
  color: #1f8559;
}

.Content-subtitle {
  text-align: left;
  margin-bottom: 0.2em;
  color: #222;
}

.Content-paragraph {
  text-align: left;
  color: #333;
  margin-top: 1em;
  margin-bottom: 0.5em;
}

.Content::after {
  display: block;
  height: 5em;
}



